.Home {
  position: relative;
  text-align: center;
  font-family: eshkol;
  overflow-y: auto;
}

.Home img {
  width: 100%;
}

@font-face {
  font-family: eshkol;
  src: url(/src/Assets/fonts/comixno2clm_bold-webfont.woff);
}

p {
  color: #7d5fb2;
  text-decoration-line: none;
  font-family: eshkol;
  font-size: larger;
}

h2,
h3 {
  margin: 4vw 1vw;
  color: #7d5fb2;
}

.carousel-img {
  max-width: 42%;
  border-right: 2px solid #7d5fb2;
  border-left: 2px solid #7d5fb2;
  border-radius: 10px;
  padding: 2px;
}

.carousel {
  border-bottom: 3px solid #7d5fb2;
  border-top: 3px solid #7d5fb2;
  border-radius: 15px;
  margin: 10px 0;
}

.Download-button {
  align-items: center;
  font-family: inherit;
  font-weight: 500;
  font-size: 17px;
  padding: 12px 20px;
  color: white;
  background: rgb(103, 92, 156);
  border: none;
  box-shadow: 0 0.7em 1.5em -0.5em rgba(59, 48, 78, 0.527);
  letter-spacing: 0.05em;
  border-radius: 8px;
  cursor: pointer;
  position: relative;
}

.Download-button svg {
  margin-right: 8px;
  width: 25px;
}

.Download-button:hover {
  box-shadow: 0 0.5em 1.5em -0.5em rgba(88, 71, 116, 0.627);
}

.Download-button:active {
  box-shadow: 0 0.3em 1em -0.5em rgba(88, 71, 116, 0.627);
}

.Download-button::before {
  content: "";
  width: 4px;
  height: 40%;
  background-color: white;
  position: absolute;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  left: 0;
  transition: all 0.2s;
}

.Download-button::after {
  content: "";
  width: 4px;
  height: 40%;
  background-color: white;
  position: absolute;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  right: 0;
  transition: all 0.2s;
}

.Download-button:hover::before,
.Download-button:hover::after {
  height: 60%;
}
